.cart {
  display: flex;
  flex-direction: column;
  padding: 10px;
  min-width: 280px;
  border-left: 1px solid #c9c9c9;
  height: calc(100vh - 70px);
  position: sticky;
  top: 70px;
  right: 0;
  background: azure;
  transition: 0.2s ease-out;

  @media (max-width: 768px) {
    position: fixed;
    top: 120px;
    height: calc(100vh - 120px);
    box-shadow: 0 -2px 4px 2px rgba(0, 0, 0, 0.229);
  }
}

.cart--hidden {
  transform: translate3d(100%, 0, 0);
  width: 0;
  min-width: 0;
}

.cart__title {
  color: #18837f;
  font-weight: 500;
  border-bottom: 1px solid #18837f29;
  padding-bottom: 10px;
  margin-bottom: 10px;
}

.cart__list {
  height: 100%;
  margin: 0 0 10px 0;
  overflow-y: auto;
}

.cartItem {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 10px;
  margin: 5px 0;
  padding: 5px;
  border-radius: 10px;
  background: #e3faf9;
}

.cartItem__linkContainer {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.cartItem__link {
  display: flex;
  align-items: center;
  gap: 5px;
}

.cartItem__image {
  width: 70px;
  height: 70px;
}

.cartItem__quantitySelector {
  display: flex;
}

.cartItem__quantity {
  margin: auto 5px;
}

.cartItem__removeBtn {
  padding: 2px;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  line-height: 1px;

  svg {
    width: 18px;
    height: 18px;
  }
}

.cart__summary {
  padding: 20px 0;
}

.cart__summaryItem {
  display: flex;
  justify-content: space-between;
  margin: 4px 0;
}

.cart__dots {
  border-bottom: dotted 3px #d7d7d7;
  flex-grow: 1;
  position: relative;
  bottom: 5px;
  margin: 0 5px;
}

.cart__checkoutBtn {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  background: #019325;
  color: #fff;
  font-weight: 600;
  border-radius: 5px;
}
